const titleCase = str => str[0].toLocaleUpperCase() + str.slice(1)
const upperCase = str => str.toUpperCase()
const lowerCase = str => str.toLowerCase()

export const pascaleCase = value => {
  if (!value) return ''
  if (typeof value.toString !== 'function') return ''

  const input = value.toString().trim()
  if (input === '') return ''
  if (input.length === 1) return input.toLocaleUpperCase()

  const match = input.match(/[a-zA-Z0-9]+/g)
  if (match) {
    return match.map(m => titleCase(m)).join('')
  }

  return input
}

export const constantCase = (input, options = {}) => {
  return noCase(input, {
    delimiter: '_',
    transform: upperCase,
    ...options
  })
}

const DEFAULT_SPLIT_REGEXP = [/([a-z0-9])([A-Z])/g, /([A-Z])([A-Z][a-z])/g]
// Remove all non-word characters.
const DEFAULT_STRIP_REGEXP = /[^A-Z0-9]+/gi

/**
 * Normalize the string into something other libraries can manipulate easier.
 */
export function noCase (input, options = {}) {
  const {
    splitRegexp = DEFAULT_SPLIT_REGEXP,
    stripRegexp = DEFAULT_STRIP_REGEXP,
    transform = lowerCase,
    delimiter = ' '
  } = options

  const result = replace(
    replace(input, splitRegexp, '$1\0$2'),
    stripRegexp,
    '\0'
  )
  let start = 0
  let end = result.length

  // Trim the delimiter from around the output string.
  while (result.charAt(start) === '\0') start++
  while (result.charAt(end - 1) === '\0') end--

  // Transform each token independently.
  return result.slice(start, end).split('\0').map(transform).join(delimiter)
}

/**
 * Replace `re` in the input string with the replacement value.
 */
function replace (input, re, value) {
  if (!Array.isArray(re)) return input.replace(re, value)
  return re.reduce((input, re) => input.replace(re, value), input)
}
