<template>
  <Dropdown
    hide-on-click
    body-width="100%"
    class="select-input"
  >
    <template #label>
      <Container
        :muted="muted"
        :bordered="bordered"
        v-bind="filteredAttrs"
        class="select-input__body"
      >
        <div class="d-flex align-items-center">
          <div v-if="$slots.prepend" class="select-input__body__prepend">
            <slot name="prepend" />
          </div>
          <slot v-if="getModalValue[valueLabel]" name="selected" :item="getModalValue" />
          <span v-else class="select-input__placeholder">{{ placeholder }}</span>
        </div>

        <div v-if="$slots.append" class="select-input__body__append">
          <slot name="append" />
        </div>
        <Icon
          v-if="icon && !$slots.append"
          :name="icon"
          class="select-input__body__append"
        />
      </Container>
    </template>
    <template #body>
      <Container
        box-shadow
        bordered
        background-color="white"
        class="select-input__options"
      >
        <div
          v-for="item in filteredOptions"
          :key="item.value"
          class="select-input__options__item"
          @click="handleChange(item)"
        >
          <slot name="option" :option="item" />
        </div>
      </Container>
    </template>
  </Dropdown>
</template>

<script>
import { computed } from 'vue'
import { useModel } from '@/utils/useModel'
import clickOutside from '@/directives/clickOutside'
import Container from '@/components/Container'
import Icon from '@/components/Icon/Icon'
import Dropdown from '@/components/UIKit/Dropdown'

export default {
  name: 'SelectInput',
  inheritAttrs: true,
  directives: {
    clickOutside
  },
  components: {
    Dropdown,
    Container,
    Icon
  },
  emits: ['update:modelValue'],
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    modelValue: {
      type: [Object, null],
      default: () => ({})
    },
    options: {
      type: Array,
      default: () => ([])
    },
    icon: {
      type: String,
      default: ''
    },
    valueLabel: {
      type: String,
      default: 'value'
    },
    muted: {
      type: Boolean,
      default: false
    },
    bordered: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit, attrs }) {
    const { value } = useModel(props, emit)

    const getModalValue = computed(() => props.modelValue || {})
    const filteredAttrs = computed(() => {
      return {
        ...attrs,
        class: ''
      }
    })
    const filteredOptions = computed(() => {
      return props
        .options
        .filter(item => item[props.valueLabel] !== (props.modelValue || {})[props.valueLabel]) ||
        []
    })

    const handleChange = (item) => {
      value.value = item
    }

    return {
      filteredOptions,
      getModalValue,
      filteredAttrs,
      handleChange
    }
  }
}
</script>

<style lang="scss">
.select-input {
  position: relative;
  cursor: pointer;

  &__body {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 20px;

    &__prepend {
      margin-right: 8px;
    }
    &__append {
      margin-left: 11px;
    }
  }

  &__placeholder {
    color: #000000;
    opacity: 0.4;
  }

  &__options {
    width: 100%;

    &__item {
      margin-bottom: 4px;
      padding: 8px 19px;

      &:first-child {
        margin-top: 8px;
      }
      &:last-child {
        margin-bottom: 8px;
      }
      &:hover {
        background-color: $selectedBackground;
      }
    }
  }
}
</style>
