import { crudStore } from '@/plugins/vuex'
import { paginationCrud } from '@/store/cruds/table'
import { columns, data } from '@/store/mock/newsMock'

export default crudStore([
  paginationCrud({
    totalCount: 123
  })
],
{
  namespaced: true,
  actions: {
    setMockHistoryData ({ commit }) {
      commit('SET_TABLE_DATA', data)
      commit('SET_TABLE_COLUMNS', columns)
    }
  }
})
