<template>
  <Text v-bind="$attrs" class="date">
    {{ formattedDate }}
  </Text>
</template>

<script>
import { computed } from 'vue'
import moment from 'moment'
import Text from '@/components/Text/Text'

export default {
  name: 'Date',
  inheritAttrs: true,
  components: { Text },
  props: {
    date: {
      type: [Date, String],
      default: ''
    },
    format: {
      type: String,
      default: ''
    }
  },
  setup (props) {
    const formattedDate = computed(() => {
      return moment(new Date(props.date)).format(props.format)
    })

    return {
      formattedDate
    }
  }
}
</script>

