<template>
  <div class="news-grid">
    <div class="news-grid__filters">
      <Input
        hide-shadow
        v-model="filters.search"
        :placeholder="t('common.search')"
        class="news-grid__filters__search"
      >
        <template #prepend>
          <Icon name="search" />
        </template>
      </Input>
      <DateRange
        v-model="filters.date"
        :placeholder-to="t('common.to')"
        :placeholder-from="t('common.from')"
        class="news-grid__filters__date-range"
      />
      <SelectInput
        muted
        :placeholder="t('common.crypto')"
        icon="chevron-down"
        class="news-grid__filters__hidden"
      />
    </div>
    <Table :data="data" :columns="columns" class="news-grid__table">
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ item, column, i }">
        <span
          v-if="column.type === 'button'"
          @click="onExpand(i)"
          class="news-grid__table__manage"
        >
          Manage
        </span>
        <Date v-else-if="column.type === 'date'" format="DD MMM YYYY HH:mm" :date="item" />
        <span v-else>{{ item }}</span>
      </template>
      <template #expand>
        <div class="news-grid__expand">
          <Tabs
            v-model="activeTab"
            :data="tabs"
            @update:model-value="onChangeTab"
          >
            <template #default="{ item }">
              {{ item.title }}
            </template>
          </Tabs>
          <div class="news-grid__expand__body">
            <Text variant="p" mb="4px" mt="20px">
              {{ t('labels.title') }}
            </Text>
            <Input hide-shadow :placeholder="t('labels.title')" />
            <Text variant="p" mb="4px" mt="20px">
              {{ t('labels.description') }}
            </Text>
            <Textarea type="textarea" hide-shadow :placeholder="t('labels.description')" />
            <Button type="primary" class="news-grid__expand-action">
              {{ t('common.saveChanges') }}
            </Button>
          </div>
        </div>
      </template>
    </Table>
    <Pagination
      v-model="page"
      :total-count="totalCount"
      :per-page="perPage"
      class="news-grid__pagination"
    />
  </div>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import { useGetters, useMutations } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'
import { useI18n } from 'vue-i18n'
import { useBus } from '@/plugins/event/useBus'
import events from '@/common/events'
import Table from '@/components/Table/Table'
import Tabs from '@/components/UIKit/Tabs'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import DateRange from '@/components/UIKit/DateRange'
import SelectInput from '@/components/UIKit/Select/SelectInput'
import Pagination from '@/components/UIKit/Pagination'
import Text from '@/components/Text/Text'
import Button from '@/components/UIKit/Button'
import Date from '@/components/Text/Date'
import Textarea from '@/components/UIKit/Textarea'

export default {
  name: 'NewsGrid',
  components: { Textarea, Date, Button, Pagination, SelectInput, DateRange, Icon, Input, Table, Tabs, Text },
  setup () {
    const emitter = useBus()
    const { t } = useI18n()
    const {
      getTableColumns: columns,
      getTableData: data,
      getTablePerPage: perPage,
      getTableTotalCount: totalCount,
      getTablePage
    } = useGetters(MODULES_NAMES.NEWS)
    const {
      SET_TABLE_PAGE: setPage
    } = useMutations(MODULES_NAMES.NEWS)

    const tabs = computed(() => [
      { title: t('languages.en'), key: 'en', icon: 'uk' },
      { title: t('languages.ru'), key: 'ru', icon: 'ru' }
    ])
    const activeTab = ref(tabs.value[0])

    const page = computed({
      get: () => getTablePage.value,
      set: setPage
    })
    const filters = reactive({
      date: {
      },
      search: ''
    })

    const onExpand = (i) => {
      emitter.emit(events.TABLE_EXPAND, i)
    }
    const onChangeTab = (tab) => {
      activeTab.value = tab
    }

    return {
      t,
      columns,
      data,
      filters,
      page,
      perPage,
      totalCount,
      tabs,
      activeTab,
      onExpand,
      onChangeTab
    }
  }
}
</script>

<style lang="scss">
.news-grid {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;

  &__filters {
    display: flex;
    width: 100%;
    height: 44px;
    margin-bottom: 20px;

    &__search {
      flex-grow: 1;
      min-width: 647px;
    }
    &__date-range {
      margin: 0 20px;
    }
  }
  &__pagination {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 50px;
  }
  &__table {
    flex-grow: 1;

    &__manage {
      display: inline;
      padding: 6px 12px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      box-sizing: border-box;
      border-radius: 4px;
      background-color: map-get($colors, "white");

      font-size: 14px;
    }
  }
  &__expand {
    padding: 10px 20px;

    &-action {
      margin-top: 20px;
      padding: 13px 16px;
      font-size: 14px;
    }
    &__body {
      max-width: 500px;
    }
  }
}
</style>
