<template>
  <section class="tabs" :style="{ '--inactive-opacity': inactiveOpacity }">
    <div
      v-for="item in data"
      :key="item.key"
      :class="{ tabs__item_active: modelValue.key === item.key }"
      class="tabs__item"
      @click="$emit('update:modelValue', item)"
    >
      <Icon :name="item.icon" class="tabs__icon"/>
      <slot :item="item" />
    </div>
  </section>
</template>

<script>
import Icon from '@/components/Icon/Icon'
export default {
  components: { Icon },
  emits: ['update:modelValue'],
  props: {
    data: {
      type: Array,
      require: true
    },
    inactiveOpacity: {
      type: [String, Number],
      default: '1'
    },
    modelValue: {
      type: Object,
      default: () => ({})
    }
  }
}

</script>

<style lang="scss">
.tabs {
  display: flex;
  align-items: baseline;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  &__icon {
    margin-right: 10px;
    margin-bottom: 2px;
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 14px 0;
    margin-right: 30px;
    opacity: var(--inactive-opacity);

    font-size: 14px;
    line-height: 15px;
    cursor: pointer;

    &_active {
      position: relative;
      opacity: 1;

      &:before {
        content: '';
        position: absolute;
