<template>
  <Container
    bordered
    box-shadow
    variant="label"
    class="textarea"
    :class="{
      textarea__error: error,
      'textarea__read-only': readOnly,
      'textarea__hide-shadow': hideShadow || readOnly
    }"
    :style="{
      '--opacity': fullOpacity
    }"
  >
    <div class="textarea__body">
      <span class="textarea__body-label">{{ label }}</span>
      <span
        v-if="showPlaceholder"
        class="textarea__placeholder"
        :style="placeholderStyles"
      >{{ placeholder }}</span>
      <div
        contenteditable="true"
        class="textarea__body-field"
        :class="{'textarea__body-field__opacity': opacity}"
        :readonly="readOnly"
        :disabled="disabled"
        :type="type"
        @blur="$emit('blur')"
        @input="onInput"
      />
    </div>
    <div v-if="$slots.append" class="textarea__append">
      <slot name="append" />
    </div>
    <div
      v-if="errorText && error"
      class="textarea__error-text"
    >
      {{ errorText }}
    </div>
  </Container>
</template>

<script>
import { computed, ref } from 'vue'
import Container from '@/components/Container'

export default {
  name: 'Textarea',
  components: { Container },
  emits: ['update:modelValue', 'blur'],
  inheritAttrs: true,
  props: {
    type: {
      type: String,
      default: 'text'
    },
    label: {
      type: String,
      default: ''
    },
    modelValue: {
      type: [String, Number],
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    placeholderStyles: {
      type: Object,
      default: () => ({})
    },
    errorText: {
      type: String,
      default: ''
    },
    error: {
      type: Boolean,
      default: false
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    opacity: {
      type: Boolean,
      default: false
    },
    fullOpacity: {
      type: [Number, String],
      default: 1
    },
    hideShadow: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit }) {
    const value = ref(props.modelValue)
    const textareaValue = computed({
      get: () => props.modelValue || value.value,
      set (val) {
        value.value = val
        emit('update:modelValue', val)
      }
    })
    const showPlaceholder = computed(() => !value.value)
    const onInput = (event) => {
      textareaValue.value = event.target.innerText
    }

    return {
      showPlaceholder,
      textareaValue,
      onInput
    }
  }
}
</script>

<style lang="scss">
.textarea {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 14px 9px 20px;

  &__body {
    position: relative;
    width: 100%;

    &-label {
      display: block;

      font-size: 14px;
      line-height: 16px;

      color: #000000;
      opacity: 0.4;
    }
    &-field {
      min-height: 100px;
      height: auto;
      resize: vertical;
      width: 100%;
      margin-left: -4px;
      border: none;
      outline: none;

      font-size: 14px;
      line-height: 16px;

      color: map-get($colors, "black");
      padding-top: 4px;
      background: transparent;

      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
      &[type=number] {
        -moz-appearance: textfield;
      }
    }
  }
  &__placeholder {
    position: absolute;
    top: 4px;
    margin-left: -3px;
    padding-top: 4px;
    opacity: 0.4;
  }
  &__prepend {
    margin-right: 10px;
  }
  &__error {
    border: 1px solid map-get($colors, "error");
    box-shadow: $boxShadowError;

    &-text {
      position: absolute;
      top: calc(100% + 6px);
      left: 0;

      font-size: 12px;
      line-height: 14px;
      color: map-get($colors, "error");
    }
  }
  & > div {
    opacity: var(--opacity);
  }
}
.textarea__read-only.textarea {
  background-color: #FAFAFA;

  &__body {
    &-field {
      &__opacity {
        opacity: 0.4;
      }
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: linear-gradient(transparent 150px, white);
      }
    }
  }
}
.textarea__hide-shadow.textarea {
  box-shadow: none;
}
</style>
